
/*landing page*/

.header-color {
  background: #A2FEC7; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #A2FEC7,
    #FBD786,
    #f7797d
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.icon > a {
  color: white;
  font-size: 2em;
  padding: 10px;
}

.landing-grid {
  background: #833ab4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #A2FEC7,
    #FBD786,
    #f7797d
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 100%;
  min-height: 90vh;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}

.profile-img {
  height: 350px;
  width: 350px;
}



.banner-text {
  background-color: black;
  opacity: 0.9;
  width: 90%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
}

.coding {
  padding: 1em;
}

.landing-buttonBox
{
  padding-bottom: 1em;
}

/*login page*/

.login-content {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  background-color: #F1F1F1;
}

.login-box {
  height: 450px;
  margin-top: 3em;
  margin-bottom: 3em;
  padding: 30px;
  background-color:#FFF;
  border-color: #DFDFDE ;
  border-style: solid;
}

.login-form {
  display: flex;
  flex-direction: column;

}

.login-input {
  width:300px;
  height:30px;
  font-size:18px;
}

.login-logo {
  display: flex;
  width: 100%;
  justify-content: center;
}


/*contact page*/

.contact-body {
  justify-content: center;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 95%;
  max-width: 1000px;
  color: black;
}

/* leftside */
.contact-left-col {
  background: #ABCDF3 ; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #ABCDF3,
    #FFF
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}


/* 반응형 사진 크기 */
.contactPic-img {
  width: 100%;
  max-width: 400px;
  height: auto;
}

.contact-grid h2 {
  font-family: "Rockwell", sans-serif;
}

.contact-grid p {
  font-family: "Goudy Old Style";
  font-weight: bold;
}

/* rightside */

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

/* resume page */


.resume-profile-img {
  border-radius: 50%;
  height: 250px;
  width: 250px;
  padding: 1em;
}

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* customers page */
.customers-table {
  margin: auto;
  width: 60%;
  padding-top: 4em;
}

